.header {
    padding: 46px 0 56px;
}

.header__row{
    display: flex;
    column-gap: 30px;
    align-items: center;
    justify-content: space-between;
}

.header__logo {
    display: flex;
    align-items: center;
    column-gap: 13px;
}

.header__logo span {
    font-size: 23px;
    font-weight: 900;
    line-height: 1.5;
    letter-spacing:0.03em;
    text-transform: uppercase;
}
.header__nav {
    font-weight: 600;
    font-size: 15px;
    line-height: 1.5;
    letter-spacing: 0.01em;
    font-style: normal;
}

.header__nav ul {
    display: flex;
    column-gap: 30px;
    align-items: center;
}
.header__nav a{
    color: var(--textColor);
}

.header__nav-btn, a.header__nav-btn   {
    padding: 13px 22px 17px;    
    background-color: black;
    border-radius: 7px;
    
    font-weight: 500;
    font-size: 24px;
    line-height: 1.5;
    letter-spacing: -0.03em;
    color: #fff;
}